module.exports = ".form-bg{\n    background: #00b4ef;\n}\n.form-horizontal{\n    background: #fff;\n    padding-bottom: 40px;\n    border-radius: 15px;\n    text-align: center;\n}\n.form-horizontal .heading{\n    display: block;\n    font-size: 35px;\n    font-weight: 700;\n    padding: 35px 0;\n    border-bottom: 1px solid #f0f0f0;\n    margin-bottom: 30px;\n}\n.form-horizontal .form-group{\n    padding: 0 40px;\n    margin: 0 0 25px 0;\n    position: relative;\n}\n.form-horizontal .form-control{\n    background: #f0f0f0;\n    border: none;\n    border-radius: 20px;\n    box-shadow: none;\n    padding: 0 20px 0 45px;\n    height: 40px;\n    transition: all 0.3s ease 0s;\n}\n.form-horizontal .form-control:focus{\n    background: #e0e0e0;\n    box-shadow: none;\n    outline: 0 none;\n}\n.form-horizontal .form-group i{\n    position: absolute;\n    top: 12px;\n    left: 60px;\n    font-size: 17px;\n    color: #c8c8c8;\n    transition : all 0.5s ease 0s;\n}\n.form-horizontal .form-control:focus + i{\n    color: #00b4ef;\n}\n.form-horizontal .fa-question-circle{\n    display: inline-block;\n    position: absolute;\n    top: 12px;\n    right: 60px;\n    font-size: 20px;\n    color: #808080;\n    transition: all 0.5s ease 0s;\n}\n.form-horizontal .fa-question-circle:hover{\n    color: #000;\n}\n.form-horizontal .main-checkbox{\n    float: left;\n    width: 20px;\n    height: 20px;\n    background: #11a3fc;\n    border-radius: 50%;\n    position: relative;\n    margin: 5px 0 0 5px;\n    border: 1px solid #11a3fc;\n}\n.form-horizontal .main-checkbox label{\n    width: 20px;\n    height: 20px;\n    position: absolute;\n    top: 0;\n    left: 0;\n    cursor: pointer;\n}\n.form-horizontal .main-checkbox label:after{\n    content: \"\";\n    width: 10px;\n    height: 5px;\n    position: absolute;\n    top: 5px;\n    left: 4px;\n    border: 3px solid #fff;\n    border-top: none;\n    border-right: none;\n    background: transparent;\n    opacity: 0;\n    -webkit-transform: rotat